import React, {useRef} from 'react';
import emailjs from '@emailjs/browser';



export default function Contact() {
    const emailjs_templateId = process.env.EMAILJS_SERVICE_ID;
    const emailjs_serviceId = process.env.EMAILJS_TEMPLATE_ID;
    const emailjs_publicKey = process.env.EMAILJS_TEMPLATE_ID;

    const form = useRef();

    // const sendEmail = (e) => {
    //   e.preventDefault();
        
    //   emailjs.sendForm(
    //     process.env.EMAILJS_SERVICE_ID,
    //     process.env.EMAILJS_TEMPLATE_ID,
    //     form.current,
    //     process.env.EMAILJS_PUBLIC_KEY
    //   ).then(
    //     result => console.log(result.text),
    //     error => console.log(error.text)
    //   );
    //   e.target.reset();
    // };


    const sendEmail = (e) => {
        e.preventDefault();
    
        emailjs
          .sendForm(process.env.EMAILJS_PUBLIC_KEY, 'template_tt269am', form.current, {
            publicKey: '8_Ann4yQcT-f2kzE0',
          })
          .then(
            () => {
              console.log('SUCCESS!');
            },
            (error) => {
              console.log('FAILED...', error.text);
            },
          );
      };

  return (
    <div className='bg-red-200'>
        <form ref={form} onSubmit={sendEmail}>
            <label>Name</label>
                <input type="text" name="user_name" />
            <label>Email</label>
            <input type="email" name="user_email" />
            <label>Message</label>
                <textarea name="message" />
            <input type="submit" value="Send" />
        </form>
    </div>
  )
}
